const firebaseConfig = {
  apiKey: "AIzaSyDnODxmR-518-jNZhGb2ZKQLj6AzTY0vbA",
  authDomain: "tasneem-sahat.firebaseapp.com",
  projectId: "tasneem-sahat",
  storageBucket: "tasneem-sahat.appspot.com",
  messagingSenderId: "844136519718",
  appId: "1:844136519718:web:5bf90465c949191b65aee7"
};

// Initialize Firebase
initializeApp(firebaseConfig);

import { useState } from "react";

export default function Authentication() {
  const [user, setUser] = useState({
    name: "",
    password: "",
    email: "",
    error: "",
    success: false
  });
  function HandleSubmit(e) {
    if (user.email && user.password) {
      const auth = getAuth();
      createUserWithEmailAndPassword(auth, user.email, user.password)
        .then((userCredential) => {
          console.log(userCredential);
          const newUserInfo = { ...user };
          newUserInfo.error = "";
          newUserInfo.success = true;
          setUser(newUserInfo);
        })
        .catch((error) => {
          const newUserInfo = { ...user };

          newUserInfo.success = false;
          newUserInfo.error = error.message;
          setUser(newUserInfo);
        });
    }
    e.preventDefault();
  }
  function HandleCNG(e) {
    let isFieldValid = true;
    if (e.target.name === "email") {
      isFieldValid = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(
        e.target.value
      );
    }
    if (e.target.name === "password") {
      isFieldValid = e.target.value.length > 5;
    }
    if (e.target.name === "name") {
      isFieldValid = e.target.value.length > 0;
    }
    if (isFieldValid) {
      const newUserInfo = { ...user };
      newUserInfo[e.target.name] = e.target.value;
      setUser(newUserInfo);
    } else {
      const newUserInfo = { ...user };
      newUserInfo[e.target.name] = "";
      setUser(newUserInfo);
    }
    console.log(isFieldValid);
  }
  return (
    <div>
      Our own Authentication
      <form onSubmit={HandleSubmit}>
        <input
          required
          name="name"
          type="text"
          onBlur={HandleCNG}
          placeholder="Enter your name"
        ></input>
        <br></br>
        <input
          required
          name="email"
          type="text"
          onBlur={HandleCNG}
          placeholder="Enter your email"
        ></input>
        <br></br>
        <input
          required
          name="password"
          type="password"
          onBlur={HandleCNG}
          placeholder="Enter your password"
        ></input>
        <br></br>
        <input type="submit"></input>
      </form>
      <p style={{ color: "red" }}>{user.error}</p>
      {user.success && <p style={{ color: "green" }}>Sign up successful</p>}
    </div>
  );
}
